import {
	mapState
} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue'
import HMfilterDropdown from '@/components/HM-filterDropdown/HM-filterDropdown.vue';
import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
import uniSwipeActionItem from '@/components/uni-swipe-action-item/uni-swipe-action-item.vue'
import uniList from '@/components/uni-list/uni-list';
import uniListItem from "@/components/uni-list-item/uni-list-item.vue"
import uniTag from "@/components/uni-tag/uni-tag.vue"
export default {
	components: {
		uniNumberBox,
		uniSwipeAction,
		uniSwipeActionItem,
		uniList,
		uniListItem,
		uniTag,
		HMfilterDropdown
	},
	data() {
		return {
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
			menuData: [],
			menuBorder: false,
			menuArrow: false,
			menuCard: false,
			skin: false,
			filterDropdownValue: [],
			options: [{
				text: '查看',
				carId: 23,
				style: {
					backgroundColor: '#006c00'
				}
			}, {
				text: '编辑',
				carId: 23,
				style: {
					backgroundColor: '#b5b55a'
				}
			}]
		};
	},
	onLoad() {
		this.loadData();
	},
	//下拉刷新
	onPullDownRefresh() {
		console.log('哥,你下拉了')
		setTimeout(() => {
			uni.stopPullDownRefresh();
		}, 3000)
	},
	//加载更多
	onReachBottom() {
		console.log('哥,你上拉了')
	},
	watch: {
		//显示空白页
		cartList(e) {
			let empty = e.length === 0 ? true : false;
			if (this.empty !== empty) {
				this.empty = empty;
			}
		}
	},
	computed: {
		...mapState(['hasLogin'])
	},
	methods: {
		//请求数据
		async loadData() {
			let cartList = await this.$api.json('goodsList');
			cartList.forEach(function(element, index) {
				// console.log(element)
				let options = [{
					text: '查看',
					carId: 23,
					style: {
						backgroundColor: '#006c00'
					}
				}, {
					text: '编辑',
					carId: 23,
					style: {
						backgroundColor: '#b5b55a'
					}
				}]
				options[0].carId = element.ID
				options[1].carId = element.ID
				element.options = options
			})
			this.cartList = cartList;
			console.log(this.cartList)
			this.menuData = await this.$api.json('menuExam');
			this.calcTotal(); //计算总价
		},
		//监听image加载完成
		onImageLoad(key, index) {
			this.$set(this[key][index], 'loaded', 'loaded');
		},
		//监听image加载失败
		onImageError(key, index) {
			this[key][index].image = '/static/errorImage.jpg';
		},
		navToLogin() {
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		onClick(e) {
			console.log('当前点击的是第' + e.index + '个按钮，点击内容是' + e.content.text)
			console.log('carId', e.content.carId)
		},
		change(open) {
			console.log('当前开启状态：' + open)
		},
		//选中状态处理
		/* check(type, index) {
			if (type === 'item') {
				this.cartList[index].checked = !this.cartList[index].checked;
			} else {
				const checked = !this.allChecked
				const list = this.cartList;
				list.forEach(item => {
					item.checked = checked;
				})
				this.allChecked = checked;
			}
			this.calcTotal(type);
		}, */
		//数量
		numberChange(data) {
			this.cartList[data.index].number = data.number;
			this.calcTotal();
		},
		//删除
		deleteCartItem(index) {
			let list = this.cartList;
			let row = list[index];
			let id = row.id;

			this.cartList.splice(index, 1);
			this.calcTotal();
			uni.hideLoading();
		},
		//清空
		clearCart() {
			uni.showModal({
				content: '清空购物车？',
				success: (e) => {
					if (e.confirm) {
						this.cartList = [];
					}
				}
			})
		},
		//计算总价
		calcTotal() {
			let list = this.cartList;
			if (list.length === 0) {
				this.empty = true;
				return;
			}
			let total = 0;
			let checked = true;
			list.forEach(item => {
				if (item.checked === true) {
					total += item.price * item.number;
				} else if (checked === true) {
					checked = false;
				}
			})
			this.allChecked = checked;
			this.total = Number(total.toFixed(2));
		},
		//创建订单
		createCar() {
			/* let list = this.cartList;
			let goodsData = [];
			list.forEach(item => {
				if (item.checked) {
					goodsData.push({
						attr_val: item.attr_val,
						number: item.number
					})
				}
			}) */
			uni.navigateTo({
				url: '/pages/car/customer'
			})
			/* uni.navigateTo({
				url: `/pages/order/createOrder?data=${JSON.stringify({
					goodsData: goodsData
				})}`
			}) */
			this.$api.msg('跳转下一页 sendData');
		}
	}
}
